import React from "react";
import * as THREE from "three"
import {GLTFLoader} from "three/examples/jsm/loaders/GLTFLoader"
import {OrbitControls} from "three/examples/jsm/controls/OrbitControls"
interface CaseProps{
    theme: any
}
interface CaseState{
}
let people = require("../model/home.glb")
function Case(props: CaseProps, state: CaseState){
    let {theme} = props
    let display:boolean = true
    React.useEffect(() => {
        if(display === true){
            display = false
            // 设置基本东东
            let scene = new THREE.Scene()
            let camera = new THREE.PerspectiveCamera(45, theme.width / theme.height, 0.1, 1000)
            camera.position.set(0, 0, 30)
            let renderer = new THREE.WebGLRenderer({
                antialias: true
            })
            renderer.setSize(theme.width, theme.height, document.getElementById("Case")?.appendChild(renderer.domElement as any))
            renderer.setClearColor(0xffffff)
            let controlCamera = new OrbitControls(camera, renderer.domElement)
            // 加载模型
            let peopleLoader = new GLTFLoader()
            peopleLoader.load(people, function(gltf){
                scene.add(gltf.scene)
                renderer.render(scene, camera)
                controlCamera.addEventListener("change", () => {
                    renderer.render(scene, camera)
                })
            })
            
        }
    }, [])
    return(
        <div className="Case" style={{position:"absolute", ...theme}}>
            <div id="Case" style={{float: "left", width: theme.width, height: theme.height}}></div>
        </div>
    )
}
export default Case